.contactBlock {
   height: auto;
   padding: 100px 0;
   background-color: var(--second-bg-color);

   .contactContainer {
      display: flex;
      flex-direction: column;

      .formWrapper {
         max-width: 500px;
         display: flex;
         flex-direction: column;
         justify-content: flex-start;
         align-items: center;

         .contactForm {
            width: 100%;

            input, textarea {
               width: 100%;
               height: 50px;
               padding: 0 20px;
               outline: none;
               margin-bottom: 20px;
               border-radius: 5px;
               font-size: 20px;
               letter-spacing: .1px;
               border: 3px solid rgba(255, 255, 255, .2);
               color: rgba(198, 201, 216, .75);
               background: 0 0;

               &::placeholder {
                  color: #717173;
               }
            }
            input {
               caret-color: #f9004d;
         }

         textarea {
            height: 150px;
            resize: none;
            font-family: 'Poppins', sans-serif;
            caret-color: #f9004d;
         }
      }

      .contactBtn {
         border: 3px solid #f9004d;
         background: #f9004d;
         cursor: pointer;
         max-width: 60%;
         width: 100%;
         height: 59px;
         color: #fff;
         font-size: 16px;
         letter-spacing: 2px;
         border-radius: 6px;
         font-weight: 600;
         transition: .3s;
         text-transform: uppercase;

         &:hover {
            border: 3px solid #f9004d;
            box-shadow: 0 10px 15px 0 rgba(249, 0, 77, .1);
            background: 0 0;
            color: #f9004d;
            transform: translateY(-5px);
         }

         @media screen and (max-width: 500px) {
            max-width: 70%;
         }
      }
   }
}

}
